@use "global" as *;


.title02 {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  gap: rem(24);

  @include mq(md) {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: rem(10);
  }
}

.title02__main {
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(61);
  font-weight: $bold;
  line-height: 1.0;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  @include mq(md) {
    font-size: rem(39);
  }
}

.title02__sub {
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  margin-bottom: rem(7);  // 位置調整

  @include mq(md) {
    font-size: rem(16);
  }
}
