@use "global" as *;


.fv2 {
  width: 100vw;  // ここだけ、幅を指定
  min-height: rem(352);
  height: calc(352px / 1440px * 100vw);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;

  @include mq(md) {
    min-height: rem(200);
    max-height: rem(200);
    height: calc(200px / 393px * 100vw);
  }
}




// =======================================
// モディファイア
// =======================================
// fv2--about (Aboutページ)
.fv2--about.fv2 {
  background: url(../images/common/about_fv-bg-pc.webp) no-repeat center center / cover;

  @include mq(md) {
    background: url(../images/common/about_fv-bg-sp.webp) no-repeat center center / cover;
    // border-radius: 0 0 30px 30px; /* 下部2角を丸くする */
  }
}
