@use "global" as *;


.page-top {
  position: relative;
  background: #FFF7EF;
  padding-top: rem(400);

  @include mq(md) {
    padding-top: rem(235);
  }
}

.page-top::after {  // 草のあしらい
  content: "";
  width: rem(200);
  height: rem(150);
  background: url(../images/common/page-top_leaf-pc.webp) no-repeat center center / cover;
  position: absolute;
  bottom: rem(10);
  right: 0;

  @include mq(md) {
    width: rem(110);
    height: rem(75);
    background: url(../images/common/page-top_leaf-sp.webp) no-repeat center center / cover;
    bottom: rem(100);
  }
}

.page-top__inner {

}

.page-top__wrap {

  @include mq(md) {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: flex-start;
    gap: rem(15);
  }
}

