@use "global" as *;


.flex-section {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(63);

  @include mq(md) {
    flex-direction: column;
    gap: rem(25);
  }
}

.flex-section__img {
  flex-grow: 1;
  aspect-ratio: 1 / 1;
  max-width: rem(508);
  width: rem(508);
  height: rem(508);
  position: relative;

  @include mq(md) {
    max-width: rem(240);
    width: rem(240);
    height: rem(240);
  }
}

.flex-section__img img {
  aspect-ratio: 1 / 1;
  max-width: rem(468);
  width: rem(468);
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 30;

  @include mq(md) {
    max-width: rem(221);
    width: rem(221);
  }
}

.flex-section__img::after {  // 影の色
  content: "";
  aspect-ratio: 1 / 1;
  max-width: rem(468);
  width: rem(468);
  height: rem(468);
  background: rgba(238, 213, 206, 0.40);
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;

  @include mq(md) {
    max-width: rem(221);
    width: rem(221);
    height: rem(221);
  }
}

.flex-section__texts {

}

.flex-section__title {

}

.flex-section__text {
  margin-top: rem(48);
  color: var(--402-e-32, #402E32);
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  line-height: 2.0;

  @include mq(md) {
    margin-top: rem(24);
    font-size: rem(14);
  }
}
